// Prices
const useFetch = (url, options) => {
  const [data, setData] = React.useState({ response:null, wbnb: null});

  useEffect(() => {
    const fetchData = async () => {
      const result = await Axios(
        url,
      );
 
      setData(result.data);
    };
 
    fetchData();
  }, [url]);
  const err = null;
  return {data, err};
};

export const usePriceBnbBusd = (): BigNumber => {
  const url = "https://api.coingecko.com/api/v3/simple/price?ids=wbnb&vs_currencies=usd"
  const { data, err} = useFetch(url, null);
  let output = new BigNumber(0);
  if(data.wbnb){
    output = new BigNumber(data.wbnb.usd);
  }

  console.log("debug->usePriceBnbBusd", output);
  return output;
}

